import React from 'react'
const { Octokit } = require('@octokit/rest')

const octokit = new Octokit({
  auth: 'f550e69fc6954a796b2e7fcb7bc916a2c5adb114'
})

class Index extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      items: [],
      posts: [],
      downloads: [],
      postsMax: 20,
      downloadsMax: 20,
      detail: null,
      postIndex: null,
      downloadIndex: null,
    }
    this.repo = {
      owner: 'cxsnxlt338',
      repo: 'monitor-db',
      headers: {
        'If-None-Match': ''
      }
    }
  }

  componentDidMount() {
    window.App = this
    this.octokit = octokit
    this.getData()
  }

  async getData() {
    let posts = await this.octokit.repos.getContent(
      Object.assign(this.repo, { path: 'posts.json' })
    )
    let downloads = await this.octokit.repos.getContent(
      Object.assign(this.repo, { path: 'downloads.json' })
    )
    try {
      posts = decodeURIComponent(escape(atob(posts.data.content)))
      posts = JSON.parse(posts).reverse()
      downloads = decodeURIComponent(escape(atob(downloads.data.content)))
      downloads = JSON.parse(downloads).reverse()
      this.setState({ posts: posts, downloads: downloads })
    } catch (err) {
      console.log(err)
    }
  }

  onClick(type, index) {
    console.log(type, index)
    if (type == 'post') {
      if (this.state.postIndex == index) {
        this.setState({ postIndex: null })
      } else {
        this.setState({ postIndex: index })
      }
    } else {
      if (this.state.downloadIndex == index) {
        this.setState({ downloadIndex: null })
      } else {
        this.setState({ downloadIndex: index })
      }
    }
  }

  postDetailFormat(data) {
    let item = data
    console.log(item)
    const keys = ['original', 'title', 'category', 'image', 'article', 'tags', 'videoType', 'video', 'link', 'duration', 'clickCount', 'src', 'siteName', 'linkCount', 'date']
    try {
      item.data.ip = JSON.parse(item.data.ip)
    } catch (err) { }
    item.data.wait = item.data.wait / 60
    const res = {}
    for (let key of keys) {
      res[key] = item.data.item[key]
    }
    item.data.item = res
    return item
  }

  downloadDetailFormat(data) {
    let item = data
    const keys = ['image', 'url', 'videoType', 'duration', 'clickCount']
    try {
      item.data.ip = JSON.parse(item.data.ip)
    } catch (err) { }
    item.data.items = item.data.items.map((i) => {
      const res = {}
      for (let key of keys) {
        res[key] = i[key]
      }
      return res
    })
    return item
  }

  timeFormat(timestamp) {
    const time = new Date(timestamp)
    let year = time.getFullYear()
    let month = '' + (time.getMonth() + 1)
    let day = '' + time.getDate()
    let hour = '' + time.getHours()
    let min = '' + time.getMinutes()
    if (month.length < 2)
      month = '0' + month
    if (day.length < 2)
      day = '0' + day
    if (hour.length < 2)
      hour = '0' + hour
    if (min.length < 2)
      min = '0' + min
    return `${year}-${month}-${day} ${hour}:${min}`
  }

  render() {
    return (
      <div>
        <title>Monitor</title>
        <style>
        </style>
        <div className="ui centered grid">
          <div className="ten wide column">
            <h1 id="title"><i className="fas fa-tachometer-alt fa-fw"></i>&nbsp;Monitor</h1>

            <h1>Posts</h1>
            <p>{ this.state.posts.filter(i => i.error).length } error</p>
            <table className="ui unstackable table">
              <thead>
                <tr>
                  <th className="status ">Status</th>
                  <th className="timestamp">Time</th>
                  <th>Blog</th>
                </tr>
              </thead>
              <tbody>
                { this.state.posts.slice(0, this.state.postsMax).map((post, i) => {
                  return (
                    <tr key={ i }
                      className={ this.state.postIndex === i ? 'select' : (post.error ? 'negative' : 'positive') }
                      onClick={ this.onClick.bind(this, 'post', i) }
                    >
                      <td className="status center aligned">
                        <i className={ post.error ? 'icon close' : 'icon checkmark' } />
                      </td>
                      <td className="timestamp">{ this.timeFormat(post.timestamp) }</td>
                      { !post.error &&
                        <td data-label="blog">{ post.data.item.article }</td>
                      }
                      { post.error &&
                        <td data-label="blog">{ post.data.blog }</td>
                      }
                    </tr>
                  )
                })}
              </tbody>
            </table>
            { this.state.postIndex !== null &&
              <pre>{ JSON.stringify(this.postDetailFormat(this.state.posts[this.state.postIndex]), null, 2) }</pre>
            }

            <h1>Downloads</h1>
            <p>{ this.state.downloads.filter(i => i.error).length } error</p>
            <table className="ui unstackable table">
              <thead>
                <tr>
                  <th className="status">Status</th>
                  <th className="timestamp">Time</th>
                  <th>Key</th>
                  <th>Page</th>
                </tr>
              </thead>
              <tbody>
                { this.state.downloads.slice(0, this.state.downloadsMax).map((download, i) => {
                  return (
                    <tr key={ i }
                      className={ this.state.downloadIndex === i ? 'select' : (download.error ? 'negative' : 'positive') }
                      onClick={ this.onClick.bind(this, 'crawl', i) }
                    >
                      <td className="status center aligned">
                        <i className={ download.error ? 'icon close' : 'icon checkmark' } />
                      </td>
                      <td className="timestamp">{ this.timeFormat(download.timestamp) }</td>
                      <td data-label="key">{ download.data.key }</td>
                      <td data-label="count">{ download.data.current }</td>
                    </tr>
                  )
                })}
              </tbody>
            </table>
            { this.state.downloadIndex !== null &&
              <pre>{ JSON.stringify(this.downloadDetailFormat(this.state.downloads[this.state.downloadIndex]), null, 2) }</pre>
            }

            <div className="ui modal">
              <div className="header">Header</div>
              <div className="content">
                <p>fafjaeojfaoie</p>
              </div>
              <div className="actions">
                <div className="ui approve button">Approve</div>
                <div className="ui button">Neutral</div>
                <div className="ui cancel button">Cancel</div>
              </div>
            </div>


          </div>
        </div>
      </div>
    )
  }
}

export default Index